/* eslint no-undef: 0 */
import React, { useState } from "react";
import HitachiMap from "./HitachiMap";
import TagaMap from "./TagaMap";

function RealtimeAllBus() {
  const stopdata = [
    {name: '', id: 1833},
    {name: '', id: 1825},
    {name: '', id: 2118},
    {name: '', id: 1964},
    {name: '', id: 1995},
    {name: '', id: 1998},
    {name: '', id: 1844},
    {name: '', id: 1835},
    {name: '', id: 1978},
    {name: '', id: 1874},
    {name: '', id: 2293}
  ]

  const poledata = [
    {name: '1', id: 1},
    {name: '2', id: 2},
    {name: '3', id: 3},
    {name: '4', id: 4}
  ]

  const nameToStopID = (stop) => {
    for (const value of stopdata) {
      if (value.name === stop) {
        return value.id;
      }
    }
    console.log('Error: ' + stop + 'is not found.');
    return -1;
  }

  const nameToPoleID = (pole) => {
    for (const value of poledata) {
      if (value.name === pole) {
        return value.id;
      }
    }
    console.log('Error: ' + pole + 'is not found.');
    return -1;
  }

  const [stop, setStop] = useState(nameToStopID(''));
  const [pole, setPole] = useState(1);

  // stop 
  const doChangeStop = (event) => {
    setStop(nameToStopID(event.target.value));
  }
  
  // To 
  const doChangePole = (event) => {
    setPole(nameToPoleID(event.target.value));
  }

  const doClick = () => {
    var url = 'https://mc.bus-vision.jp/ibako/view/approachSpecifiedStop.html?';
    
    url += 'stopCdSpecified=' + stop + '&';
    url += 'poleCdSpecified=' + pole + '&';
    window.location.href = url;
  }

  return (
    <div className='container'>
      ()
      <div className="input-group my-3">
        <select className="form-control col" onChange={doChangeStop} defaultValue="">
          {stopdata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
        <select className="form-control col" onChange={doChangePole} defaultValue="1">
          {poledata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
      </div>
      <button type="button" className="btn btn-primary btn mb-3" onClick={doClick}></button>
      {stop === nameToStopID('') && <HitachiMap />}
      {stop === nameToStopID('') && <TagaMap />}
    </div>
  )
}

export default RealtimeAllBus