/* eslint no-undef: 0 */
import React, { useState } from 'react';
import {
  Chart as ChartJS,
  registerables
} from 'chart.js';
import { Chart } from 'react-chartjs-2';

ChartJS.register(...registerables);

const getMonthAndDay = (dateString) => {
  const date = new Date(dateString);
  const month = date.getMonth() + 1;
  const day = date.getDate();
  return month + '/' + day;
}

const getTime = (dateString) => {
  const date = new Date(dateString);
  const hour = date.getHours();
  return hour;
}

const weathercode2str = (code) => {
  switch(code) {
    case 0:
      return '';
    case 1:
      return '';
    case 2:
      return '';
    case 3:
      return '';
    case 45:
      return '';
    case 48:
      return '';
    case 51:
    case 53:
    case 55:
      return '';
    case 56:
      return '';
    case 57:
      return '';
    case 61:
      return '';
    case 63:
      return '';
    case 65:
      return '';
    case 66:
      return '';
    case 67:
      return '';
    case 71:
      return '';
    case 73:
      return '';
    case 75:
      return '';
    case 77:
      return '';
    case 80:
      return '';
    case 81:
      return '';
    case 82:
      return '';
    case 85:
      return '';
    case 86:
      return '';
    case 96:
      return '';
    case 99:
      return '';
    default:
      return 'weathercode : ' + code;
  }
}

function WeatherGraph({
                        timeData, 
                        temperatureData,      // 
                        relativehumidityData, // 
                        apparentTemperatureData, // 
                        precipitationProbabilityData,
                        precipitationData,
                        weathercodeData
                      }) {
  const [isApparent,setIsApparent] = useState(false);
  const [isViewDetail, setIsViewDetail] = useState(false);

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top' ,
      },
    },
    scales: {
      x: {
        grid: {
          display: false
        }
      },
      temperatureY: {
        position: 'left',
        min: 10,
        max: 45,
        grid: {
          display: true
        },
      },
      precipitationY: {
        position: 'right',
        min: 0,
        max: 10,
        grid: {
          display: false
        },
      },
      precipitationProbabilityY: {
        position: 'right',
        min: 0,
        max: 100,
        grid: {
          display: false
        },
      },
    },
  };

  const labels = timeData.map((item) => (getTime(item)));

  const graphData = {
    labels,
    datasets: [
      {
        label: '()',
        data: !isApparent ? temperatureData : apparentTemperatureData,
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
        type: 'line',
        yAxisID: 'temperatureY',
      },
      {
        label: '(mm)',
        data: precipitationData,
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
        type: 'bar',
        yAxisID: 'precipitationY',
      },
      {
        label: '(%)',
        data: precipitationProbabilityData,
        borderColor: 'rgb(53, 235, 162)',
        backgroundColor: 'rgba(53, 235, 162, 0.5)',
        type: 'line',
        yAxisID: 'precipitationProbabilityY',
      },
    ],
  };
  
  return (
    <div className='container'>
      <div className="form-check">
        <input 
          type="checkbox" 
          className="form-check-input"
          checked={isApparent} 
          onChange={() => setIsApparent(prevState => !prevState)} 
        />
        <label className="form-check-label">
          
        </label>
      </div>
      <Chart options={options} data={graphData} />
      <div className="form-check">
        <input 
          type="checkbox" 
          className="form-check-input"
          checked={isViewDetail} 
          onChange={() => setIsViewDetail(prevState => !prevState)} 
        />
        <label className="form-check-label">
          
        </label>
      </div>
      {isViewDetail && 
        <ul>
          <li>, , , , , , </li>
          {timeData.map((item, index) => (
            <li key={index}>
              {getMonthAndDay(item) + ' ' + getTime(item)}, {temperatureData[index] + ''}, {relativehumidityData[index] + '%'}, {apparentTemperatureData[index] + ''}, {precipitationProbabilityData[index] + '%'}, {precipitationData[index] + 'mm'}, {weathercode2str(weathercodeData[index])}
            </li>
          ))}
        </ul>
      }
    </div>
  );
}

export default WeatherGraph;