/* eslint no-undef: 0 */
import React, { useState } from "react";
import DateTimePicker from "./DateTimePicker";

function Train() {
  var currentDate = new Date();

  const stationdata = [
    {name: '', id: ''},
    {name: '', id: ''},
    {name: '', id: ''}
  ]

  const nameToID = (station) => {
    for (const value of stationdata) {
      if (value.name === station) {
        return value.id;
      }
    }
    console.log('Error: ' + station + 'is not found.');
    return -1;
  }

  const zeroPadding = (num) => {
    if (num < 10) {
      return '0' + num;
    }
    else {
      return num;
    }
  }

  const [from, setFrom] = useState(nameToID(''));
  const [to, setTo] = useState(nameToID(''));
  const [year, setYear] = useState(currentDate.getFullYear());
  const [month, setMonth] = useState(currentDate.getMonth() + 1);
  const [day, setDay] = useState(currentDate.getDate());
  const [hour, setHour] = useState(currentDate.getHours());
  const [min, setMin] = useState(currentDate.getMinutes());
  const [isReverse, setIsReverse] = useState(false);

  // From 
  const doChangeFrom = (event) => {
    setFrom(nameToID(event.target.value));
  }
  
  // To 
  const doChangeTo = (event) => {
    setTo(nameToID(event.target.value));
  }

  const doClick = () => {
    var url = 'https://www.jorudan.co.jp/time/timetable/';
    
    // 
    if (isReverse) {
      url += to + '//' + from + '/?';
    }
    else {
      url += from + '//' + to + '/?';
    }
    url += 'Dym=' + year + zeroPadding(month) + '&';
    url += 'Ddd=' + day;

    window.location.href = url;
  }

  return (
    <div className='container'>
      
      <div className="input-group my-3">
        <select className="form-control col" onChange={doChangeFrom} defaultValue="">
          {stationdata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
        {!isReverse ?
          <button className="input-group-text" 
                  onClick={() => setIsReverse(prevState => !prevState)}></button>
        :
          <button className="input-group-text" 
                  onClick={() => setIsReverse(prevState => !prevState)}></button>
        }
        <select className="form-control col" onChange={doChangeTo} defaultValue="">
          {stationdata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
      </div>
      <DateTimePicker cName={"form-control my-3"}
                      setYear={setYear} 
                      setMonth={setMonth} 
                      setDay={setDay}
                      setHour={setHour}
                      setMinute={setMin}/>
      <button type="button" className="btn btn-primary btn mb-3" onClick={doClick}></button>
    </div>
  )
}

export default Train